import { XMarkIcon } from "@heroicons/react/24/outline";
import TransparentButton from "components/buttons/Transparent";
import { twMerge } from "tailwind-merge";
import { createPortal } from "react-dom";
import BasicContainer from "components/containers/Basic";
import { cogBordersMini } from "assets/borders";
import { useEffect, useRef, useState } from "react";
type Props = {
  open: boolean;
  /** If true, the modal can be closed by clicking on the overlay */
  closableOnOverlayClick?: boolean;
  bodyClassName?: string;
  outmostClassName?: string;
  title?: string;
  loading?: boolean;
  close: () => void;
} & React.HTMLAttributes<HTMLDivElement>;

const modalRoot = document.getElementById("modal-root");

/** This is a basic modal component that can be used to create a modal with a title and a close button */
export const BasicModal: React.FC<Props> = ({
  title,
  open,
  children,
  className,
  bodyClassName,
  outmostClassName,
  loading,
  closableOnOverlayClick,
  close,
  ...attributes
}) => {
  const bgRef = useRef<HTMLDivElement>(null);
  if (!modalRoot) return <></>;
  return createPortal(
    <div
      onClick={() => closableOnOverlayClick && close()}
      className={twMerge(
        "no-scrollbar transparent absolute top-0 left-0 z-10 overflow-x-hidden overflow-y-scroll py-20 px-3 bg-neutral3/60",
        "max-h-full min-h-full w-full transition-all duration-1000",
        outmostClassName,
        open ? "block" : "hidden"
      )}
      ref={bgRef}
    >
      <BasicContainer
        noStyles
        style={cogBordersMini}
        loading={loading}
        onClick={(e) => e.stopPropagation()}
        className={twMerge(
          " relative z-40 m-auto flex h-fit max-h-full sm:min-w-[600px] sm:max-w-[80%] translate-y-[-120%] transition-all duration-500",
          "flex-col overflow-hidden rounded-xl  bg-neutral3 p-0 text-contrast2 shadow-[0_0_35px] shadow-primary/50",
          open && "translate-y-0",
          className
        )}
        {...attributes}
      >
        {/** Represents the header with the title and shows the close button */}
        {title && (
          <div className="z-50 flex items-center justify-between p-2 border-b-2 border-neutral2">
            <div className="px-4 ">{title}</div>
            <TransparentButton
              className="rounded-3xl text-contrast hover:bg-neutral2"
              onClick={() => close()}
            >
              <XMarkIcon
                strokeWidth={2}
                className={twMerge(
                  "w-6 rounded-full",
                  "transition-all hover:scale-110 "
                )}
              />
            </TransparentButton>
          </div>
        )}
        <div
          className={twMerge("border-t-2 border-neutral p-4", bodyClassName)}
        >
          {children}
        </div>
      </BasicContainer>
    </div>,
    modalRoot
  );
};
export default BasicModal;
