import { cogBordersMini } from "assets/borders";
import ImageDiv from "components/others/ImageDiv";
import { ProjectMetadata } from "pages/Projects/types";
import { mdConverter } from "utils/markdownConverter";

type Props = {
  project: ProjectMetadata["metadata"];
  url?: string;
};
const MAX_CHARACTERS = 128;
export const ProjectCardTooltip: React.FC<Props> = ({ project, url }) => {
  const desc =
    `<div class="text-contrast2 text-xl">${project.short_name}</div>\n --- \n` +
    (project.description || `<i>No description</i>`);
  const trimmedDescription =
    desc.length >= MAX_CHARACTERS
      ? desc.slice(0, MAX_CHARACTERS) + "<b>[...continues]</b>"
      : desc;

  return (
    <>
      {url && (
        <ImageDiv
          src={
            new URL(project.thumbnail, window.location.origin + url + "/").href
          }
          className="w-full aspect-video"
          style={cogBordersMini}
          loading="lazy"
        />
      )}
      <div className="max-w-sm p-2 whitespace-normal">
        {trimmedDescription ? (
          <div
            className="markdown"
            dangerouslySetInnerHTML={{
              __html: mdConverter.makeHtml(trimmedDescription),
            }}
          />
        ) : (
          <i>No description</i>
        )}
      </div>
    </>
  );
};
