import SidebarButton from "layouts/Main/Sidebar/SidebarButton";
import routes from "layouts/Main/routes";
import { useLocation, useNavigate } from "react-router-dom";
import { twMerge } from "tailwind-merge";

type Props = { className?: string };
export const Sidebar: React.FC<Props> = ({ className }) => {
  const navigate = useNavigate();
  const location = useLocation();

  return (
    <div
      className={twMerge(
        " mt-2 hidden h-fit grid-cols-[auto] gap-4 md:grid",
        className
      )}
    >
      {routes.map(([path, alias]) => (
        <SidebarButton
          key={alias}
          selected={path === location.pathname.split("/")[1]}
          onClick={() => navigate(`/${path}`)}
        >
          {alias}
        </SidebarButton>
      ))}
    </div>
  );
};
