import bgStars from "assets/images/bg-stars.png";
import bgSky1 from "assets/images/bg-sky1.png";
import bgSky2 from "assets/images/bg-sky2.png";
// import bgSky3 from "assets/images/bg-sky3.png";
import tailwindMergedColors from "theme.tailwind";

export const Background: React.FC = () => {
  return (
    <div className="absolute inset-0 flex flex-col bg-neutral2">
      <div
        className="grow animate-moving-bg"
        style={{ backgroundImage: `url(${bgStars})`,
        boxShadow:`0px -20px 20px inset ${tailwindMergedColors["neutral2"]}`
      }}
      />
      <div
        className="h-[96px]"
        style={{
          backgroundImage: `url(${bgSky1})`,
          backgroundRepeat: "repeat-x",
        }}
      />
      <div
        className="h-[96px]"
        style={{
          backgroundImage: `url(${bgSky2})`,
          backgroundRepeat: "repeat-x",
        }}
      />
    </div>
  );
};
