import { cogBordersMini } from "assets/borders";
import ImageDiv from "components/others/ImageDiv";
import Tooltip from "components/popovers/Tooltip";
import { ProjectCardTooltip } from "pages/Projects/ProjectCard/ProjectCardTooltip";
import { ProjectMetadata } from "pages/Projects/types";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { relativeTimeFromDates } from "utils/dateFormatter";

type Props = ProjectMetadata & { isFocus?: boolean };
export const ProjectCardMini: React.FC<Props> = ({
  url,
  metadata,
  id,
  isFocus,
}) => {
  const navigate = useNavigate();
  const [isHovered, setIsHovered] = useState(false);
  const {
    instructions,
    creation_date,
    description,
    width,
    height,
    name,
    short_name,
    short_description,
    resources,
    links,
    thumbnail,
    start_url,
  } = metadata;
  return (
    <div
      onClick={() => navigate("/projects/" + (isFocus ? "focus/" : "") + id)}
      role="button"
      className="flex max-w-full gap-2 p-1 group"
      onMouseLeave={() => {
        setIsHovered(false);
      }}
      onMouseEnter={() => {
        setIsHovered(true);
      }}
    >
      <Tooltip
        tooltipWrapperClass="pointer-events-none"
        placement="left"
        tip={<ProjectCardTooltip project={metadata} url={url} />}
        className="relative flex shrink"
      >
        <ImageDiv
          src={new URL(thumbnail, window.location.origin + url + "/").href}
          className="w-24 h-16"
          style={cogBordersMini}
          loading="lazy"
        />
      </Tooltip>

      <div className="flex flex-col min-w-0 p-1 text-sm transition-all">
        <div
          className="min-w-0 overflow-hidden"
          style={{ ...(isHovered ? { textShadow: "0px 0px 10px white" } : {}) }}
        >
          <span className="max-w-full min-w-0">{short_name}</span>
        </div>
        <div className="text-sm text-contrast2">{creation_date}</div>
      </div>
    </div>
  );
};
