import { cogBordersMini } from "assets/borders";
import monitorStand from "assets/images/monitor-stand.png";
import { useState } from "react";
import { twMerge } from "tailwind-merge";
import tailwindMergedColors from "theme.tailwind";

type Props = {
  selected?: boolean;
  children?: React.ReactNode;
} & React.DetailedHTMLProps<
  React.HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>;

export const SidebarButton: React.FC<Props> = ({
  children,
  selected,
  className,
  ...attributes
}) => {
  const [isHovered, setIsHovered] = useState(false);
  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className={twMerge(
        "relative transition-all whitespace-nowrap",
        (selected) && "translate-x-2",
        className
      )}
      {...attributes}
    >
      <img
        alt=""
        src={monitorStand}
        className="absolute left-0 h-8 -translate-x-full top-1"
      />
      <div
        className={twMerge(
          "relative z-20 flex bg-neutral3 px-4 text-lg transition-all",
          selected && "bg-neutral2",
          isHovered && "bg-secondary"
        )}
        style={cogBordersMini}
        role="button"
        tabIndex={0}
      >
        <span
          style={
            (selected||isHovered)
              ? {
                  textShadow: `0px 0px 15px ${tailwindMergedColors["contrast2"]}`,
                }
              : {}
          }
        >
          {children}
        </span>
      </div>
    </div>
  );
};
export default SidebarButton;
