import bar from "assets/images/bar.png";
import bar2 from "assets/images/bar2.png";
import { twMerge } from "tailwind-merge";

type Props = {
  retracted: boolean;
  left?: boolean;
};
export const RetractableBar: React.FC<Props> = ({
  retracted,
  left = false,
}) => {
  return (
    <div className={"absolute z-0 h-full w-full"}>
    <div
      style={{ backgroundImage: `url(${bar2})` }}
      className={twMerge(
        "absolute h-full w-5 bg-contain transition-all delay-700 duration-700 opacity-40",
        left ? "left-8" : "right-4",
        retracted && (left ? "-translate-y-full" : "translate-y-full")
      )}
    />
    <div
      style={{ backgroundImage: `url(${bar})` }}
      className={twMerge(
        "absolute h-full w-5 bg-contain transition-all delay-700 duration-700",
        left ? "left-6" : "right-6",
        retracted && (left ? "-translate-y-full" : "translate-y-full")
      )}
    />
    </div>
  );
};
