import logo from "assets/images/logo.png";
import cog from "assets/images/cog.png";
import cog2 from "assets/images/cog2.png";
import wall1 from "assets/images/wall1.png";
import wall2 from "assets/images/wall2.png";
import woodTile from "assets/images/wood-tile.png";
import { RetractableBar } from "layouts/Main/RetractableBar";
import { MaintenanceHpCounter } from "components/others/MaintenanceHpCounter";
import { twMerge } from "tailwind-merge";
import { MiniNavbar } from "layouts/Main/MiniNavbar";

type Props = {
  maintenance: { hp: number; maxHp: number };
  isLoading?: boolean;
};

export const LayoutGates: React.FC<Props> = ({ maintenance, isLoading }) => {
  const wallBackground: React.CSSProperties = {
    backgroundImage: `url(${woodTile})`,
  };
  const closedGates = isLoading || !!maintenance.hp;
  const navbarHeight = "h-14";
  return (
    <div className="absolute inset-0 z-20 flex flex-col pointer-events-none">
      {/** Header */}
      <div
        className={twMerge(
          "pointer-events-auto z-20 flex flex-col items-start justify-end",
          "bg-neutral3 text-contrast transition-all duration-1000 ease-out",
          navbarHeight,
          closedGates && "h-full sm:h-full"
        )}
        style={{ ...wallBackground, backgroundPosition: "bottom" }}
      >
        <div
          style={{ textShadow: "0px 0px 16px white" }}
          className={twMerge(
            "px-font relative bottom-10 flex h-0 w-full items-center text-center",
            "select-none justify-center text-2xl transition-all duration-500",
            !closedGates && "opacity-0"
          )}
        >
          {maintenance.hp ? <>REVAMPING IN PROGRESS</> : "Loading..."}
        </div>
        <div
          className={twMerge(
            "relative flex w-full items-baseline justify-between px-6",
            navbarHeight
          )}
        >
          <div className="absolute top-0 bottom-0 right-2 ">
            <MiniNavbar className={twMerge("opacity-100 transition-all duration-500",closedGates&&"opacity-0")}/>
          </div>
          <div
            className={twMerge(
              "flex h-full grow-0 justify-center transition-all duration-1000",
              closedGates && "mb-8 grow"
            )}
          >
            <div
              className={
                "px-font flex select-none items-baseline text-xs sm:text-base"
              }
            >
              <img
                src={logo}
                alt="logo"
                className="object-contain w-64 pt-4 "
              />
              <span className="hidden sm:inline">.github.io</span>
            </div>
          </div>
          {/* Cog right */}
          <div
            className={twMerge(
              "absolute bottom-0 right-0 z-20 translate-x-1/3 translate-y-1/2 pt-16 transition-all delay-500 duration-1000",
              !closedGates && "translate-x-[150%]"
            )}
          >
            <div className="relative w-32 h-32">
              <img
                src={cog2}
                alt="cog shadow"
                className="absolute inset-0 object-contain w-full top-2 animate-spin"
              />
              <img
                src={cog}
                alt="cog"
                className="absolute inset-0 object-contain w-full animate-spin"
              />
            </div>
          </div>
        </div>

        <div
          className={twMerge(
            "h-8 w-full bg-contain transition-all delay-500 duration-500",
            !closedGates && "h-0 translate-x-full"
          )}
          style={{ backgroundImage: `url(${wall1})` }}
        />
      </div>
      {/** Spaceholder */}
      <div
        className={twMerge(
          "pointer-events-none relative grow bg-transparent transition-all duration-1000"
        )}
      >
        <RetractableBar retracted={!closedGates} left />
        <RetractableBar retracted={!closedGates} />
      </div>
      {/** Footer */}
      <div
        className={twMerge(
          " relative h-0 bg-neutral3 transition-all duration-1000",
          "pointer-events-auto ease-out",
          closedGates && "h-full"
        )}
        style={wallBackground}
      >
        {/* Cog left */}
        <div
          className={twMerge(
            "absolute left-0 z-20 -translate-x-1/2 -translate-y-1/2 transition-all delay-500 duration-1000",
            !closedGates && "-translate-x-[150%]"
          )}
        >
          <div className="relative w-32 h-32">
            <img
              src={cog2}
              alt="cog shadow"
              className="absolute inset-0 object-contain w-full top-2 animate-spin2"
            />
            <img
              src={cog}
              alt="cog"
              className="absolute inset-0 object-contain w-full animate-spin2"
            />
          </div>
        </div>
        <div
          className={twMerge(
            "h-8 w-full bg-contain transition-all delay-500 duration-500",
            !closedGates && "-translate-x-full"
          )}
          style={{ backgroundImage: `url(${wall2})` }}
        />
        <MaintenanceHpCounter maintenance={maintenance} />
      </div>
    </div>
  );
};
