import BasicModal from "components/modals/Basic";
import { useState } from "react";
import { twMerge } from "tailwind-merge";
import defaultPlaceholder from "assets/images/placeholder_px.png";

type ImageProps = React.DetailedHTMLProps<
  React.ImgHTMLAttributes<HTMLImageElement>,
  HTMLImageElement
>;
type Props = {
  placeholder?: string;
  withModal?: boolean;
  imageClassName?: string;
  containerClassName?: string;
  src: string | null | undefined;
} & Omit<ImageProps, "src">;

export const ImageDiv: React.FC<Props> = ({
  className,
  src,
  placeholder = defaultPlaceholder,
  title,
  withModal,
  imageClassName,
  containerClassName,
  ...attributes
}) => {
  const [open, setOpen] = useState(false);
  return (
    <>
      <div
        className={twMerge(
          "shrink-0 overflow-hidden",
          withModal && "cursor-zoom-in",
          className,
          containerClassName
        )}
        role={"img"}
        onClick={() => setOpen(true)}
      >
        <InnerImage
          className={twMerge(className, "h-full w-full", imageClassName)}
          {...{ ...attributes, src, placeholder }}
        />
      </div>
      {withModal && (
        <BasicModal
          open={open}
          title={title}
          close={() => setOpen(false)}
          closableOnOverlayClick
        >
          <div className={"sm:min-h-[300px] w-full"}>
            <InnerImage
              preview
              {...{
                ...attributes,
                src,
                className,
                placeholder,
              }}
            />
          </div>
        </BasicModal>
      )}
    </>
  );
};

const InnerImage: React.FC<
  {
    preview?: boolean;
    placeholder?: string;
    src: ImageProps["src"] | null | undefined;
  } & Omit<ImageProps, "src">
> = ({ preview = false, placeholder, className, src, ...attributes }) => {
  const [isLoaded, setIsLoaded] = useState(false);
  return (
    <img
      className={twMerge(
        "object-cover opacity-0 transition-none",
        preview ? "h-full w-full rounded-2xl object-contain" : className,
        isLoaded && "opacity-100"
      )}
      alt="placeholder"
      src={String(src)}
      onLoad={(e) => {
        setIsLoaded(true);
      }}
      {...attributes}
    />
  );
};
export default ImageDiv;
