import { cogBordersMini } from "assets/borders";
import { OptionsBar } from "pages/Projects/OptionsBar";
import { ProjectFrameOverlay } from "pages/Projects/ProjectFrame/ProjectFrameOverlay";
import { ProjectMetadata } from "pages/Projects/types";
import { useEffect, useRef, useState } from "react";
import logoOutline from "assets/images/logo1_outline.png";
import { twMerge } from "tailwind-merge";
import BasicButton from "components/buttons/Basic";
import ImageDiv from "components/others/ImageDiv";

type Props = {
  project: ProjectMetadata;
  src: string;
  className?: string;
};
const disableLogs = true;
export const ProjectFrame: React.FC<Props> = ({ project, src, className }) => {
  const [isLoaded, setIsLoaded] = useState(false);
  const frameContainerRef = useRef<HTMLDivElement>(null);
  const frameRef = useRef<HTMLIFrameElement>(null);
  const { name, width, height } = project.metadata;
  const requiredWidth = 680 + Number(width);
  const [clientWidth, setClientWidth] = useState(
    window.document.body.clientWidth
  );
  const tooBig = clientWidth < requiredWidth;
  const [isFullscreen, setIsFullscreen] = useState(false);
  const thumb = new URL(
    project.metadata.thumbnail,
    window.location.origin + project.url + "/"
  ).href;
  useEffect(() => {
    if (frameRef.current?.contentWindow) {
      // Disables console logs from the iframe
      if (disableLogs) {
        // @ts-ignore
        frameRef.current.contentWindow.console.log = () => {};
      }
    }
  }, [frameRef]);
  useEffect(() => {
    setIsLoaded(false);
  }, [name]);
  useEffect(() => {
    document.onfullscreenchange = () => {
      setIsFullscreen(document.fullscreenElement ? true : false);
      const iframe = frameRef.current;
      if (iframe) iframe.src = String(iframe.src);
    };
    window.onresize = () => setClientWidth(window.document.body.clientWidth);
  }, []);
  return (
    <>
      <div className="relative">
        <div
          style={{
            ...cogBordersMini,
            ...(!tooBig
              ? {
                  height: height + "px",
                  width: width + "px",
                }
              : {}),
          }}
          ref={frameContainerRef}
          className={twMerge(
            "relative flex items-center justify-center overflow-hidden",
            tooBig && "aspect-square w-full",
            isFullscreen && "h-full w-full",
            className
          )}
        >
          <iframe
            src={src}
            title={name}
            width={isFullscreen ? "100%" : width}
            height={isFullscreen ? "100%" : height}
            className={twMerge(
              "bg-contrast",
              tooBig && !isFullscreen && "hidden"
            )}
            onLoad={() => setIsLoaded(true)}
            ref={frameRef}
          ></iframe>
          <ImageDiv
            src={thumb}
            className={twMerge(
              "h-full w-full rounded",
              (!tooBig || isFullscreen) && "hidden"
            )}
          />
          <div
            className={twMerge(
              "pointer-events-none absolute inset-0 bg-neutral3 opacity-100 transition-all delay-500 duration-1000",
              isLoaded && "opacity-0"
            )}
          />
          {!tooBig && <OptionsBar frameRef={frameRef} />}
          <ProjectFrameOverlay isLoaded={isLoaded} />
        </div>
        <div className="absolute left-0 right-0 z-0 flex justify-center pointer-events-none -bottom-1 ">
          <img src={logoOutline} className="h-4" alt="decoration" />
        </div>
      </div>
      {clientWidth < requiredWidth && (
        <div className="flex flex-col items-center gap-2 text-center">
          <BasicButton
            className="inline mx-2"
            onClick={() => {
              // Launches project in fullscreen
              const cont = frameContainerRef.current;
              if (!cont) return;
              cont.requestFullscreen();
            }}
          >
            Launch (fullscreen)
          </BasicButton>
          <small>
            The project window cannot fit in this resolution, but you can try to
            launch it anyway on fullscreen.
          </small>
        </div>
      )}
    </>
  );
};
