import fullHeart from "assets/images/others/heart.png";
import halfHeart from "assets/images/others/half-heart.png";
import emptyHeart from "assets/images/others/empty-heart.png";
import { twMerge } from "tailwind-merge";
import { useState } from "react";

type Props = {
  maintenance: { hp: number; maxHp: number };
};

const heartSprites = [emptyHeart, halfHeart, fullHeart];
export const MaintenanceHpCounter: React.FC<Props> = ({
  maintenance: { hp, maxHp },
}) => {
  const hearts = new Array(Math.ceil(maxHp / 2))
    .fill(0)
    .map((_, i) => {
      let isFullHeart = hp >= (i + 1) * 2;
      let isHalfHeart = hp > i * 2;
      if (isFullHeart) return 2;
      if (isHalfHeart) return 1;
      return 0;
    })
    .reverse();

  return (
    <div className="flex justify-center w-full gap-4 py-8 select-none">
      {hearts.map((heart, i) => {
        return (
          <img
            key={i}
            src={heartSprites[heart]}
            alt="heart"
            className={twMerge(
              "h-14 opacity-50 transition-all",
              heart === 1 && "animate-shake",
              heart && "opacity-75",
              heart > 1 && "opacity-100",
              heart === 0 && "animate-ping-once",
            )}
          />
        );
      })}
    </div>
  );
};
