import { useParams } from "react-router-dom";
import projectsJSON from "assets/projects.json";
import { cogBorders } from "assets/borders";
import { ProjectFrame } from "pages/Projects/ProjectFrame";
import { useEffect, useRef, useState } from "react";
import { twMerge } from "tailwind-merge";
import useTitle from "hooks/useTitle";
import ImageDiv from "components/others/ImageDiv";
import { MetadataEditor } from "pages/Projects/MetadataEditor";
import { mdConverter } from "utils/markdownConverter";
import "./markdown.tailwind.css";
import { ProjectCardMini } from "pages/Projects/ProjectCard/ProjectCardMini";
import BasicButton from "components/buttons/Basic";
import { serverUrl } from "utils/constants";
import { currentEnv } from "utils/currentEnv";
import monitorStand from "assets/images/monitor-stand.png";

const projects = projectsJSON;

const BREAKPOINT_CONT = 1450;

export const Project: React.FC = () => {
  const { id } = useParams();
  const project = projectsJSON.find((p) => String(p.id) === String(id));
  useTitle(project?.metadata.short_name || "Not found");

  if (!project) return <div></div>;
  const {
    instructions,
    creation_date,
    description,
    width,
    height,
    name,
    short_name,
    short_description,
    resources,
    links,
    thumbnail,
    start_url,
  } = project.metadata;
  const isFocus = window.location.pathname.split("/").includes("focus");
  return (
    <div className={twMerge("-m-3 mt-0 flex grow justify-center")}>
      <div
        className={twMerge(
          "markdown flex grow overflow-y-scroll",
          "relative text-contrast",
          "lg:flex-row lg:justify-center",
          "flex-col justify-start"
        )}
        style={{ maxWidth: BREAKPOINT_CONT }}
        key={project.id}
      >
        <div
          className="flex flex-col items-center gap-6 m-6 top-full h-fit grow bg-neutral3"
          style={cogBorders}
        >
          {name && (
            <div className="flex items-end justify-between w-full pb-2 mb-0 -m-5 border-b border-contrast2">
              <h2 className="!mb-0 shrink text-contrast">{name}</h2>
              <span className="!mb-0 text-contrast">{creation_date}</span>
            </div>
          )}
          <ProjectFrame
            src={
              new URL(start_url, window.location.origin + project.url + "/")
                .href
            }
            project={project}
          />
          {currentEnv === "development" && (
            <div className="flex gap-3">
              <MetadataEditor project={project} />
              <BasicButton
                onClick={async () => {
                  const openVSCRequest = serverUrl + "/code/" + project.id;
                  const res = await fetch(openVSCRequest);
                  console.log({ openCodeRequestRes: res });
                }}
              >
                OPEN VSCODE
              </BasicButton>
            </div>
          )}
          <div
            className="flex flex-col w-full gap-4"
            // style={{ minWidth: 800, width: width + "px" }}
            style={{ maxWidth: Number(width) > 800 ? width : 800 + "px" }}
          >
            {instructions && (
              <div>
                <h2 className=" text-contrast2">Instructions</h2>
                <hr />
                <div className="px-0 sm:px-4">
                  {instructions && (
                    <div
                      dangerouslySetInnerHTML={{
                        __html: mdConverter.makeHtml(instructions),
                      }}
                    />
                  )}
                </div>
              </div>
            )}
            {description && (
              <div>
                <h2 className=" text-contrast2">Description</h2>
                <hr />
                <div className="px-0 sm:px-4">
                  {description && (
                    <div
                      className="markdown"
                      dangerouslySetInnerHTML={{
                        __html: mdConverter.makeHtml(description),
                      }}
                    />
                  )}
                </div>
              </div>
            )}
            {resources.length > 0 && (
              <div>
                <h2 className=" text-contrast2">Resources</h2>
                <hr />
                <div className="flex flex-wrap gap-3 px-0 sm:px-4">
                  {resources.map((resourceUrl) => {
                    return (
                      <div className="relative h-fit w-fit" key={resourceUrl}>
                        <ImageDiv
                          src={
                            new URL(
                              resourceUrl,
                              window.location.origin + project.url + "/"
                            ).href
                          }
                          withModal
                          className="w-64 h-64 rounded-lg"
                          title={resourceUrl}
                        />
                        <div className="absolute bottom-0 left-0 right-0 p-1 px-2 rounded-b bg-neutral2">
                          {resourceUrl}
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            )}
            <div className="fixed top-0"></div>
          </div>
        </div>
        {/** Projects previews */}

        <div
          className={twMerge(
            "m-6 flex h-fit shrink-0 flex-wrap gap-6 bg-neutral3",
            "relative items-stretch lg:w-[300px]"
          )}
          style={{ ...cogBorders }}
        >
          <img
            className="absolute top-0 left-0 hidden h-12 -translate-x-[170%] md:block"
            alt="decoration"
            src={monitorStand}
          />
          <img
            className="absolute top-[400px] left-0 hidden h-12 -translate-x-[170%] md:block"
            alt="decoration"
            src={monitorStand}
          />
          <div className="relative flex flex-col items-stretch -m-4 shrink grow">
            <div className="mb-2 pointer-events-none bg-neutral2">
              <ProjectCardMini key={project.id} {...project} />
            </div>
            {projects
              .filter((p) => p.id !== Number(id))
              .map((project) => (
                <ProjectCardMini
                  key={project.id}
                  {...project}
                  isFocus={isFocus}
                />
              ))}
          </div>
        </div>
      </div>
    </div>
  );
};
export default Project;
