// import monitorStand from "assets/images/monitor-stand.png";
import { cogBorders, cogBordersMini } from "assets/borders";
import monitorStand from "assets/images/monitor-stand.png";
import useTitle from "hooks/useTitle";
import { ProjectCard } from "pages/Projects/ProjectCard";
import { useEffect, useState } from "react";
import projectsJSON from "assets/projects.json";
import { HeaderPage, MAX_PAGE_WIDTH } from "layouts/HeaderPage";
const projects = projectsJSON.reverse();
const gridConfig = {
  maxContWidth: MAX_PAGE_WIDTH,
  breakPoint: 700,
  maxSteps: 4,
  step: 240,
};
const prepareGrid = (clientWidth: number) => {
  const avWidth = clientWidth - gridConfig.breakPoint;
  const finalWidth =
    avWidth > gridConfig.maxContWidth ? gridConfig.maxContWidth : avWidth;
  const columnsRaw = Math.ceil(finalWidth / gridConfig.step);
  const columns =
    columnsRaw > 1
      ? columnsRaw > gridConfig.maxSteps
        ? gridConfig.maxSteps
        : columnsRaw
      : 1;
  const rows = Math.ceil(projects.length / columns);
  return [columns, rows];
};
export const Projects: React.FC = () => {
  useTitle("Projects");
  const [clientWidth, setClientWidth] = useState(
    window.document.body.clientWidth
  );
  const [columns, rows] = prepareGrid(clientWidth);
  useEffect(() => {
    window.onresize = () => setClientWidth(window.document.body.clientWidth);
  }, []);
  return (
      <HeaderPage
        header={
          <>
            <div className="text-xl font-bold text-contrast2">Projects</div>
            <div>
              A list of my web-based projects from most recent to oldest. Note
              that most of them won't support mobile controls{" "}
            </div>
          </>
        }
      >
        {new Array(rows).fill(0).map((_, i) => {
          return (
            <div className="flex justify-between w-full gap-4" key={`row-${i}`}>
              {new Array(columns).fill(0).map((_, j) => {
                const project = projects[i * columns + j];
                if (!project)
                  return (
                    <div
                      key={i * columns + j}
                      className="aspect-[6/5] flex-1"
                    />
                  );
                return (
                  <ProjectCard
                    key={i * columns + j}
                    {...project}
                    className="aspect-[6/5] w-full min-w-0 flex-1 [&_img]:opacity-80 [&_img]:hover:opacity-100"
                  />
                );
              })}
            </div>
          );
        })}
      </HeaderPage>
  );
};
export default Projects;
