import Pages from "pages";
import MainLayout from "layouts/Main";
import { Routes, Route, Navigate } from "react-router-dom";
import { useEffect, useState } from "react";
console.log(process.env);
const MIN_TIME = 300;
function App() {
  const [minTimePassed, setMinTimePassed] = useState(false);

  useEffect(() => {
    setTimeout(() => setMinTimePassed(true), MIN_TIME);
  }, []);
  return (
    <Routes>
      <Route index element={<Navigate to="main" />} />
      <Route path="" element={<MainLayout isLoading={!minTimePassed} />}>
        <Route path="main" element={<Pages.Main />} />
        <Route path="projects" element={<Pages.Projects />} />
        <Route path="projects/:id" element={<Pages.Project />} />
        <Route path="resources" element={<Pages.Resources />} />
        <Route path="about" element={<Pages.About />} />
      </Route>
      <Route
        path="projects/focus/:id"
        element={
          <div className="absolute inset-0 flex justify-center p-3 bg-neutral3">
            <div className="absolute z-0 bottom-2 left-4 text-neutral">
              saantonandre.github.io
            </div>
            <Pages.Project />
          </div>
        }
      />
    </Routes>
  );
}

export default App;
