import { cogBordersMini } from "assets/borders";
import optionsCog from "assets/images/cog4.png";
import BasicButton from "components/buttons/Basic";
import Tooltip from "components/popovers/Tooltip";
import { useEffect, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { twMerge } from "tailwind-merge";

type Props = {
  frameRef: React.RefObject<HTMLIFrameElement>;
};
export const OptionsBar: React.FC<Props> = ({ frameRef }) => {
  const location = useLocation();
  const { id } = useParams();
  const navigate = useNavigate();
  const [hovered, setHovered] = useState(false);
  useEffect(() => {
    if (frameRef.current) {
      frameRef.current.onmouseenter = () => setHovered(false);
      // Buggy on mobile
      // frameRef.current.focus();
    }
  }, [frameRef]);
  const isFocus = location.pathname.split("/").includes("focus");
  return (
    <>
      <div
        className={twMerge(
          "absolute bottom-0 w-full translate-y-[105%] bg-neutral3 p-4 transition-all",
          hovered && "translate-y-0"
        )}
        style={{
          ...cogBordersMini,
          borderLeft: 0,
          borderRight: 0,
          borderBottom: 0,
        }}
        // onMouseLeave={() => setHovered(false)}
      >
        <div className="flex gap-3 ml-20">
          <Tooltip tip="Launches the project's window in full screen">
            <BasicButton onClick={() => frameRef.current?.requestFullscreen()}>
              Fullscreen
            </BasicButton>
          </Tooltip>
          <Tooltip tip="Enables/Disables some UI elements on page (warning: RELOADS THE PAGE)">
            {isFocus ? (
              <BasicButton
                onClick={() =>
                  navigate(location.pathname.replace("focus/" + id, String(id)))
                }
              >
                Unfocus
              </BasicButton>
            ) : (
              <BasicButton
                onClick={() =>
                  navigate(location.pathname.replace(String(id), "focus/" + id))
                }
              >
                Focus mode
              </BasicButton>
            )}
          </Tooltip>
        </div>
      </div>
      <div
        className={twMerge(
          "absolute bottom-4 left-4 w-fit shrink-0 opacity-80",
          hovered && "opacity-100"
        )}
        onTouchStart={() => setHovered((prev) => !prev)}
        onMouseEnter={() => setHovered(true)}
        role="button"
      >
        <img alt="decoration" src={optionsCog} />
      </div>
    </>
  );
};
