import { cogBordersMini } from "assets/borders";
import ImageDiv from "components/others/ImageDiv";
import Tooltip from "components/popovers/Tooltip";
import { ProjectCardTooltip } from "pages/Projects/ProjectCard/ProjectCardTooltip";
import { ProjectMetadata } from "pages/Projects/types";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { twMerge } from "tailwind-merge";
import { relativeTimeFromDates } from "utils/dateFormatter";

type Props = ProjectMetadata&{className?:string};
export const ProjectCard: React.FC<Props> = ({ url, metadata, id,className }) => {
  const navigate = useNavigate();
  const [isHovered, setIsHovered] = useState(false);
  const {
    instructions,
    creation_date,
    description,
    width,
    height,
    name,
    short_name,
    short_description,
    resources,
    links,
    thumbnail,
    start_url,
  } = metadata;
  return (
    <div
      onClick={() => navigate("/projects/" + id)}
      role="button"
      className={twMerge("flex flex-col p-1 group max-w-full",className)}
      onMouseLeave={() => {
        setIsHovered(false);
      }}
      onMouseEnter={() => {
        setIsHovered(true);
      }}
    >
      <Tooltip
        tooltipWrapperClass="pointer-events-none"
        tip={<ProjectCardTooltip project={metadata} />}
        className="relative flex grow"
      >
        <div className="absolute inset-0 overflow-hidden"
            style={cogBordersMini}>
          <ImageDiv
            src={new URL(thumbnail, window.location.origin + url + "/").href}
            className={twMerge(
              "transition-all duration-200 absolute inset-0",
              isHovered && "scale-105"
            )}
            loading="lazy"
          />
        </div>
      </Tooltip>

      <div className="flex flex-col min-w-0 p-1 transition-all">
        <div
          className="min-w-0 overflow-hidden text-lg text-ellipsis"
          style={{ ...(isHovered ? { textShadow: "0px 0px 10px white" } : {}) }}
        >
          <span className="max-w-full min-w-0 whitespace-nowrap">{short_name}</span>
        </div>
        <div className="text-sm text-contrast2 sm:text-base">
          {creation_date}<span className="hidden sm:inline"> - {relativeTimeFromDates(new Date(creation_date))}</span>
        </div>
      </div>
    </div>
  );
};
