import { Outlet, useParams } from "react-router-dom";
import FixedSpaceContainer from "components/containers/FixedSpace";
import { twMerge } from "tailwind-merge";
import { Background } from "layouts/Main/Background";
import { useMaintenance } from "hooks/useMaintenance";
import { useRef, useEffect } from "react";
import { LayoutGates } from "layouts/Main/LayoutGates";
import { Sidebar } from "layouts/Main/Sidebar";

type Props = {
  asParent?: boolean;
  children?: React.ReactNode;
  isLoading?: boolean;
};
export const MainLayout: React.FC<Props> = ({
  asParent,
  children,
  isLoading,
}) => {
  const { id } = useParams();
  const scrollRef = useRef<HTMLDivElement>(null);
  const maintenance = useMaintenance(6);
  const { hp } = maintenance;
  const closedGates = isLoading || !!hp;
  // const footerHeight = "h-10";
  useEffect(() => {
    scrollRef.current?.scrollTo({ top: 0, behavior: "smooth" });
  }, [id]);
  return (
    <div className="relative flex flex-col w-screen h-screen max-h-screen overflow-hidden bg-neutral text-contrast">
      <Background />
      <LayoutGates isLoading={isLoading} maintenance={maintenance} />
      {/** Header spaceholder */}
      <div className={twMerge("m-1", "h-12 sm:h-14")} />
      <FixedSpaceContainer bodyClassName=" sm:ml-0">
        <div className="inset-0 flex flex-col w-full gap-2 -m-1 sm:flex-row">
          <Sidebar
            className={twMerge(
              " -translate-x-full p-4 transition-all delay-500 duration-500",
              !closedGates && "translate-x-0 text-contrast"
            )}
          />
          <div
            className="w-full overflow-x-hidden overflow-y-scroll"
            ref={scrollRef}
          >
            {asParent ? children : <Outlet />}
          </div>
        </div>
      </FixedSpaceContainer>
    </div>
  );
};
export default MainLayout;
