import { cogBordersMini } from "assets/borders";
import monitorStand from "assets/images/monitor-stand.png";
import { useState } from "react";
import { twMerge } from "tailwind-merge";
import tailwindMergedColors from "theme.tailwind";

type Props = {
  selected?: boolean;
  children?: React.ReactNode;
} & React.DetailedHTMLProps<
  React.HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>;

export const BotbarButton: React.FC<Props> = ({
  children,
  selected,
  className,
  ...attributes
}) => {
  const [isHovered, setIsHovered] = useState(false);
  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      className={twMerge(
        "relative mr-4 whitespace-nowrap transition-all",
        selected && "-translate-x-2",
        className
      )}
      {...attributes}
    >
      <div
        className={twMerge(
          "relative z-20 flex bg-neutral3 px-4 text-base transition-all",
          selected && "bg-neutral2",
          isHovered && "bg-secondary"
        )}
        style={cogBordersMini}
        role="button"
        tabIndex={0}
      >
        <span
          style={
            selected || isHovered
              ? {
                  textShadow: `0px 0px 15px ${tailwindMergedColors["contrast2"]}`,
                }
              : {}
          }
        >
          {children}
        </span>
      </div>
      <img
        alt="decoration"
        src={monitorStand}
        className="absolute right-0 top-2 h-8 translate-x-full"
      />
    </div>
  );
};
export default BotbarButton;
