import doorBorder from "assets/images/closingdoor/closingdoor_border.png";
import doorCarve from "assets/images/closingdoor/closingdoor_carve.png";
import doorCog from "assets/images/closingdoor/closingdoor_cog.png";
import { twMerge } from "tailwind-merge";

type Props = { isLoaded: boolean };
export const ProjectFrameOverlay: React.FC<Props> = ({ isLoaded }) => {
  const doorsClassClose = "delay-500 duration-500 transition-all";
  const doorClassOpen = "-translate-x-full";
  return (
    <div className="absolute inset-0 flex -m-1 pointer-events-none">
      <div
        className={twMerge(
          "z-10 flex flex-1 flex-col",
          doorsClassClose,
          isLoaded && doorClassOpen
        )}
      >
        <div className="flex flex-col grow">
          <div className="flex grow">
            <div className="grow bg-neutral2" />
            <img src={doorBorder} alt="decoration" className="w-2 " />
          </div>
          <div
            className={twMerge(
              "flex shrink-0 bg-neutral2 transition-all delay-1000 duration-500 ease-in",
              isLoaded && "-translate-x-1/2"
            )}
          >
            <div className="grow" />
            <img
              src={doorCog}
              alt="decoration"
              className={twMerge(
                "translate-x-1/2",
                isLoaded && "rotate-180 duration-1000"
              )}
            />
          </div>
          <div className="flex grow">
            <div className="grow bg-neutral2" />
            <img src={doorBorder} alt="decoration" className="w-2 " />
          </div>
        </div>
      </div>

      <div
        className={twMerge(
          "z-0 flex flex-1 flex-col",
          doorsClassClose,
          isLoaded && doorClassOpen && "translate-x-full"
        )}
      >
        <div className="flex grow">
          <img src={doorBorder} alt="decoration" className="w-2 " />
          <div className="grow bg-neutral2" />
        </div>
        <div className="flex shrink-0">
          <img src={doorCarve} alt="decoration " />
          <div className="grow bg-neutral2" />
        </div>
        <div className="flex grow">
          <img src={doorBorder} alt="decoration" className="w-2 " />
          <div className="grow bg-neutral2" />
        </div>
      </div>
    </div>
  );
};
