import { useEffect, useState } from "react";
export const useMaintenance = (maxHp: number) => {
  const [remainingHp, setRemainingHp] = useState(maxHp || 0);
  useEffect(() => {
    if (!maxHp) return;
    window.onclick = () =>
      setRemainingHp((prev) => (prev > 0 ? prev - 1 : prev));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return { hp: remainingHp, maxHp };
};
