import { useEffect, useRef, useState } from "react";
import {
  useFloating,
  autoUpdate,
  offset,
  flip,
  shift,
  useHover,
  useFocus,
  useDismiss,
  useRole,
  useInteractions,
  Placement,
  FloatingArrow,
  arrow,
} from "@floating-ui/react";
import { createPortal } from "react-dom";
import { twMerge } from "tailwind-merge";
import tailwindMergedColors from "theme.tailwind";
import { cogBorders, cogBordersMini } from "assets/borders";
const modalRoot = document.getElementById("modal-root");
type Props = {
  anchorEl?: Element;
  tip?: React.ReactNode;
  content?: React.ReactNode;
  controlled?: boolean;
  placement?: Placement;
  active?: boolean;
  disabled?: boolean;
  tooltipWrapperClass?: string;
  /** Offsets [x,y] */
  offsets?: [number, number];
} & React.DetailedHTMLProps<
  React.HTMLAttributes<HTMLDivElement>,
  HTMLDivElement
>;
const ARROW_HEIGHT = 25;
const GAP = 2;
export const Tooltip: React.FC<Props> = ({
  tip = "tooltip",
  content,
  children,
  anchorEl,
  controlled,
  placement = "top",
  active,
  offsets = [0, 0],
  disabled,
  tooltipWrapperClass,
  ...attributes
}) => {
  const [open, setOpen] = useState(false);
  const arrowRef = useRef<SVGSVGElement>(null);
  const { x, y, strategy, refs, context } = useFloating({
    open,
    onOpenChange: setOpen,
    placement: placement,
    middleware: [
      offset({ crossAxis: offsets[0], mainAxis: offsets[1] }),
      flip(),
      shift(),
      arrow({
        element: arrowRef,
      }),
    ],

    whileElementsMounted: autoUpdate,
  });
  const hover = useHover(context, { move: false });
  const focus = useFocus(context);
  const dismiss = useDismiss(context);
  const role = useRole(context, { role: "tooltip" });

  const isOpen = (controlled ? active : open) && !disabled;
  // Merge all the interactions into prop getters
  const { getReferenceProps, getFloatingProps } = useInteractions([
    hover,
    focus,
    dismiss,
    role,
  ]);
  useEffect(() => {
    if (anchorEl) anchorEl && refs.setReference(anchorEl);
  }, [anchorEl, refs]);
  return (
    <>
      {!anchorEl && (
        <div ref={refs.setReference} {...getReferenceProps()} {...attributes}>
          <>{children}</>
        </div>
      )}
      {isOpen &&
        createPortal(
          <div
            ref={refs.setFloating}
            style={{
              position: strategy,
              top: y ?? 0,
              left: x ?? 0,
              width: "max-content",
            }}
            className={twMerge(
              "tooltip z-50",
              tooltipWrapperClass,
              !(x || y) && "invisible"
            )}
            {...getFloatingProps()}
          >
            {content ? (
              <>{content}</>
            ) : (
              <div
                className={twMerge(
                  "m-2 rounded-lg border py-1 px-2 font-medium shadow shadow-primary/50",
                  "border-primary bg-neutral2 text-contrast"
                )}
              >
                <div
                  className="text-sm font-medium whitespace-nowrap"
                >
                  {tip}
                </div>
                <FloatingArrow
                  ref={arrowRef}
                  context={context}
                  className="-m-[10px] fill-neutral2 stroke-2"
                  width={ARROW_HEIGHT}
                  height={ARROW_HEIGHT/2}
                  stroke={tailwindMergedColors.primary}
                  strokeWidth={1}
                />
              </div>
            )}
          </div>,
          modalRoot!
        )}
    </>
  );
};
export default Tooltip;
