import { cogBorders, cogBordersMini } from "assets/borders";
import monitorStand from "assets/images/monitor-stand.png";

type Props = {
  header: React.ReactNode;
};
export const MAX_PAGE_WIDTH = 1200;
export const HeaderPage: React.FC<React.PropsWithChildren<Props>> = ({
  header,
  children,
}) => {
  return (
    <div className={"-m-3 mt-0 flex grow justify-center"}>
      <div
        className="relative flex flex-col items-center w-full p-6 pt-0 overflow-x-hidden overflow-y-auto"
        style={{ maxWidth: MAX_PAGE_WIDTH }}
      >
        <div className="w-full">
          <div className="relative w-full p-6">
            <div
              className="relative z-10 px-2 bg-neutral3"
              style={cogBordersMini}
            >
              {header}
            </div>
          </div>
          <div
            className="relative flex flex-wrap w-full gap-6 p-2 bg-neutral3"
            style={{ ...cogBorders }}
          >
            <img
              src={monitorStand}
              className="absolute top-0 right-0 h-6 -translate-y-[250%] rotate-90"
              alt="decoration"
            />
            <img
              src={monitorStand}
              className="absolute top-0 left-0 h-6 -translate-y-[250%] rotate-90 -scale-x-100"
              alt="decoration"
            />
            {children}
          </div>
        </div>
      </div>
    </div>
  );
};
