import BasicModal from "components/modals/Basic";
import notesPng from "assets/images/notes-icon.png";
import notesPngActive from "assets/images/notes-icon_active.png";
import { useState } from "react";
import commitsJSON from "assets/gitlog.json";
import notesBig from "assets/images/notes-big.png";
import { twMerge } from "tailwind-merge";
type Props = {
  className?: string;
};
export type GitlogCommit = typeof commitsJSON[number];
const commits = commitsJSON satisfies GitlogCommit[];
const commitsByDate = commits.reduce<Record<string, GitlogCommit[]>>(
  (acc, curr) => {
    const date = curr.authorDate.slice(0, 10);
    if (acc[date]) {
      return { ...acc, [date]: [...acc[date], curr] };
    }
    return { ...acc, [date]: [curr] };
  },
  {}
);
export const PatchNotesModal: React.FC<Props> = ({ className }) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <button className={className} onClick={() => setIsOpen((prev) => !prev)}>
        <img
          src={notesPng}
          alt="Notes icon"
          className={twMerge("h-full",isOpen&&"hidden")}
        />
        <img
          src={notesPngActive}
          alt="Notes icon"
          className={twMerge("hidden h-full",isOpen&&"block")}
        />
      </button>

      <BasicModal
        title="Website patch notes (gitlog)"
        open={isOpen}
        close={() => setIsOpen(false)}
      >
        <div className="flex flex-col">
          <div className="flex flex-col items-center gap-4 mb-4">
            <div className="text-3xl">Website patch notes</div>
            <img src={notesBig} alt="Patch notes" />
          </div>
          {Object.entries(commitsByDate).map(([date, commits]) => {
            return (
              <div className="flex flex-col w-full" key={date}>
                <code className="text-2xl">
                  {new Date(date).toUTCString().slice(5, 16)}
                </code>
                <div className="markdown">
                  <hr className="opacity-50" />
                  <ul className="pl-10 text-contrast">
                    {commits.map(({ authorDate, subject, body }) => {
                      return <li key={authorDate}>{subject}</li>;
                    })}
                  </ul>
                </div>
              </div>
            );
          })}
        </div>
      </BasicModal>
    </>
  );
};
