import routes from "layouts/Main/routes";
import { useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { twMerge } from "tailwind-merge";
import navPng from "assets/images/nav-icon.png";
import navPngActive from "assets/images/nav-icon_active.png";
import RouteButton from "layouts/Main/RoutesButton/RouteButton";

type Props = { className?: string };
export const RoutesButton: React.FC<Props> = ({ className }) => {
  const navigate = useNavigate();
  const location = useLocation();

  const [isOpen, setIsOpen] = useState(false);
  return (
    // className="fixed flex flex-col items-end gap-4 right-2 bottom-4"
    <>
      <div
        role="button"
        tabIndex={0}
        onClick={() => setIsOpen((prev) => !prev)}
        className={className}
      >
        <img
          src={navPng}
          alt="Notes icon"
          className={twMerge("h-full", isOpen && "hidden")}
        />
        <img
          src={navPngActive}
          alt="Notes icon"
          className={twMerge("hidden h-full", isOpen && "block")}
        />
      </div>
      <div
        // TODO: Add button to open navigation
        // Close navigation on route selection
        className={twMerge(
          "absolute right-2 top-20 z-10 flex w-fit flex-col gap-4",
          !isOpen && "pointer-events-none",
          className
        )}
      >
        {routes.map(([path, alias], i) => {
          const delays = ["delay-75", "delay-100", "delay-150", "delay-200"];
          return (
            <div
              className={twMerge(
                "translate-x-[120%] translate-y-full transition-all duration-300 ease-out",
                isOpen && "translate-x-0 translate-y-0",

                delays[i]
              )}
              key={alias}
            >
              <RouteButton
                selected={path === location.pathname.split("/")[1]}
                onClick={() => {
                  navigate(`/${path}`);
                  setIsOpen(false);
                }}
              >
                {alias}
              </RouteButton>
            </div>
          );
        })}
      </div>
      <div
        className={twMerge(
          "absolute top-14 -right-2 z-0 h-screen",
          "w-screen",
          isOpen ? "animate-fade-bg-in" : "animate-fade-bg-out"
        )}
        onClick={() => setIsOpen(false)}
      />
    </>
  );
};
